.sideNav {
  height: 100vh;
  width: 500px;
  background-color: #FAFAFA;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  overflow-x: hidden;
  -webkit-transition: width 0.2s;
  -o-transition: width 0.2s;
  transition: width 0.2s;
  overflow-y: scroll; }
  @media (max-width: 1260px) {
    .sideNav {
      width: 250px; } }
  @media (max-width: 864px) {
    .sideNav {
      position: absolute; } }

.headFnt {
  width: 100%;
  height: 50px;
  font-size: 250%;
  font-family: 'Fira Sans', sans-serif;
  color: #5A626A;
  font-weight: 600;
  text-align: center;
  -webkit-transition: top 0.1s;
  -o-transition: top 0.1s;
  transition: top 0.1s; }

.ulFont {
  position: relative;
  font-size: 130%;
  font-family: 'Fira Sans', sans-serif;
  color: #5A626A;
  font-weight: 600;
  text-align: left; }

a {
  text-decoration: none !important; }
  a:hover {
    text-decoration: none !important; }

li {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: #FAFAFA;
  color: #7AC8FF;
  -webkit-transition: color 0.05s;
  -o-transition: color 0.05s;
  transition: color 0.05s;
  cursor: pointer; }
  li:hover {
    text-decoration: none !important; }

li:nth-child(odd) {
  background-color: #F3F4F4; }
  li:nth-child(odd):hover {
    text-decoration: none !important; }

.ulHeadFont {
  position: relative;
  font-size: 230%;
  padding-left: 22px;
  font-family: 'Fira Sans', sans-serif;
  color: #5A626A;
  font-weight: 600;
  text-align: left;
  cursor: pointer; }

.head {
  height: 80px;
  background-color: #F3F4F4;
  top: 0;
  position: fixed;
  width: 483.5px;
  z-index: 9;
  -webkit-transition: height 0.1s, width 0.2s, -webkit-box-shadow 0.2s;
  transition: height 0.1s, width 0.2s, -webkit-box-shadow 0.2s;
  -o-transition: height 0.1s, box-shadow 0.2s, width 0.2s;
  transition: height 0.1s, box-shadow 0.2s, width 0.2s;
  transition: height 0.1s, box-shadow 0.2s, width 0.2s, -webkit-box-shadow 0.2s; }
  @media (max-width: 1260px) {
    .head {
      width: 233.5px; } }
  @media (max-width: 864px) {
    .head {
      position: absolute; } }

.contents {
  padding-top: 80px;
  position: relative;
  height: 2000px; }



